<template>
	<view class="navWrap">
		<!-- 自定义标题栏部分 -->
		<cover-view class="navBar" :style="{height: sysInfo.rheight ? sysInfo.rheight + 'px' : '120rpx',backgroundColor: navInfo.bgColor}">
			<cover-view class="contentWrap" :style="{height: sysInfo.cheight + 'px'}">
				<cover-view class="leftContent">
					<cover-view v-if="navInfo.type == 'optional'"><!-- 自选页面/小花 -->
						<cover-image class="headBtn" :src="navInfo.iconImg" v-if="navInfo.iconImg" @click="itemClick"></cover-image>
					</cover-view>
					<cover-view v-else-if="loginInfo.account"><!-- 已登录账号头像 -->
						<cover-image class="headBtn" :src="headUrl" v-if="headUrl" @click="itemClick(1)"></cover-image>
					</cover-view>
					<cover-view class="login" v-else><!-- 未登录 -->
						<button id="btnLogin" :class="[platform == 'ios' ? 'ios' : '']" :style="{backgroundColor: '#fff',color: '#000',fontSize: '30rpx'}" type="primary" open-type="getPhoneNumber"
						 @getphonenumber="getPhoneNumber">登录</button>
					</cover-view>
				</cover-view>
				<cover-view class="middleContent" :class="[platform == 'ios' ? 'ios' : '']">{{navInfo.title}}</cover-view>
				<cover-view class="wxBtn"></cover-view>
			</cover-view>
		</cover-view>
		<!-- <cover-view class="paddingBlock" :style="{height: sysInfo.rheight ? sysInfo.rheight + 'px' : '120rpx'}"></cover-view> -->
		<!-- 标题栏自定义气泡部分 -->
		<cover-view class="accountBg" v-show="showAccount && navInfo.type !== 'optional'" @click="showMenu">
			<cover-view class="accountWrap" :style="{top: sysInfo.rheight + 'px'}" @click.stop>
				<cover-view class="management accountItem" @click="itemClick(1)">
					<cover-image class="icon" src="/static/22.png"></cover-image>
					<cover-view class="text">帐号管理</cover-view>
				</cover-view>
				<cover-view class="feedback accountItem" @click="itemClick(2)">
					<cover-image class="icon" src="/static/22.png"></cover-image>
					<cover-view class="text">意见反馈</cover-view>
				</cover-view>
			</cover-view>
		</cover-view>
		<cover-view class="guidanceBg" v-if="showGuidance && navInfo.type !== 'optional'" :style="{top: sysInfo.rheight - 3 + 'px'}">
			<cover-image class="cornerWrap" :style="{right: sysInfo.meunLeft - 5 + 'px'}" src="/static/arrowGrey.png"></cover-image>
			<cover-view class="addGuidance">
				<cover-view class="contentText">
					<cover-view class="text_one">添加到我的小程序</cover-view>
					<cover-view class="text_two">同花顺-让投资更简单</cover-view>
				</cover-view>
				<cover-view class="closeWrap" @click="closeGuidance">
					<cover-image class="close" src="/static/close.png"></cover-image>
				</cover-view>
			</cover-view>
		</cover-view>
		<cover-view class="guidanceBgXH" v-else-if="showGuidanceXH && navInfo.type === 'optional'" :style="{top: sysInfo.rheight - 3 + 'px'}">
			<cover-image class="cornerWrapXH" src="/static/arrowBlack.png"></cover-image>
			<cover-view class="addGuidanceXH">
				<cover-view class="contentText">
					<cover-view class="text_one">{{ xiahuaPop.text_one }}</cover-view>
					<cover-view class="text_two">{{ xiahuaPop.text_two }}</cover-view>
				</cover-view>
				<cover-view class="closeWrap" @click="closeGuidanceXH">
					<cover-image class="close" src="/static/close.png"></cover-image>
				</cover-view>
			</cover-view>
		</cover-view>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex';
	import { hxmClickStat } from '../../utils/stat.js';
	export default {
		props: ['navInfo', 'showAccount'],
		data() {
			return {
				showGuidanceXH: false, // 显示小花气泡
				xiahuaData: [{ // 小花提示气泡文案
						text_one: '你好啊！我是您的',
						text_two: '智能投资顾问小花！'
					},{
						text_one: '想了解更多？',
						text_two: '赶紧问小花吧！'
					},{
						text_one: '我已经看透牛熊，',
						text_two: '快来和我聊聊天吧！'
					},{
						text_one: '没什么比吃顿好吃的',
						text_two: '更让人开心啦！'
					},{
						text_one: '帅气与多才不可兼得，',
						text_two: '但是恰好你都有~'
					},{
						text_one: '拍一拍我吧，',
						text_two: '我真的很博学诶~'
					},{
						text_one: '任何财经信息，',
						text_two: '都可以问我试试看哦！'
					},{
						text_one: '不知道如何选股？',
						text_two: '快来问问我吧~'
					},{
						text_one: '好久不见啊！',
						text_two: '朋友！'
					},{
						text_one: '投资这件事嘛，',
						text_two: '确实需要积累。'
					}],
			};
		},
		mounted() {
			// 小花提示气泡
			if(this.navInfo.type === 'optional') {
				let time = uni.getStorageSync('guidanceXH');
				if(time) {
					if(+new Date() - parseInt(time) >= 5*24*60*60*1000) { // 5天显示一次
						this.showGuidanceXH = true;
					}
				} else {
					this.showGuidanceXH = true;
				}
			}
		},
		computed: {
			...mapState(['sysInfo', 'loginInfo', 'platform', 'headUrl', 'showGuidance']),
			xiahuaPop() {
				let random = parseInt(Math.random()*this.xiahuaData.length)
				return this.xiahuaData[random]
			}
		},
		methods: {
			showMenu() {
				this.$emit('showMenu');
			},
			getPhoneNumber(e) {
				if (e.detail.iv && e.detail.encryptedData) {
					this.$emit('loginComplete', {
						iv: e.detail.iv,
						encryptedData: e.detail.encryptedData
					});
				} else {
					uni.showToast({
						icon: 'none',
						title: '您未授权手机号',
					})
				}
			},
			itemClick(type) {
				this.$emit('itemClick', type);
			},
			closeGuidance() {
				this.$store.commit('setShowGuidance', false);
				//存入本地存储以后不再提示
				uni.setStorageSync('guidance', true);
			},
			closeGuidanceXH() {
				hxmClickStat('qipaoclose');
				this.showGuidanceXH = false;
				//将当前日期的0点时间戳记录到本地
				let time = new Date(new Date().setHours(0,0,0,0)).getTime();
				uni.setStorageSync('guidanceXH', time);
			}
		}
	}
</script>

<style lang="less">
	.paddingBlock {
		width: 100%;
	}

	.navWrap {
		// position: relative;
		// top: 0;
		// left: 0;

		.accountBg {
			position: fixed;
			width: 100%;
			height: 100%;
			z-index: 1000;
		}
		
		.guidanceBg, .guidanceBgXH {
			position: fixed;
			top: 158rpx;
			right: 0;
			width: 380rpx;
			height: 136rpx;
			z-index: 10000;
		}
		
		.guidanceBgXH {
			left: 0;
			right: auto;
			width: 390rpx;
		}

		.accountWrap {
			
			position: fixed;
			left: 32rpx;
			top: 150rpx;
			width: 248rpx;
			height: 192rpx;
			background-color: #5b5b5b;
			border-radius: 16rpx;
			box-shadow: 0 2rpx 8rpx 4rpx rgba(85, 85, 85, 0.10);
			border: 1px solid rgba(85, 85, 85, 0.10);
			// box-shadow: 0 2rpx 8rpx 4rpx #f5f5f5;
			z-index: 1000;
			display: flex;
			flex-direction: column;
			color: #fff;

			.accountItem {
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 30rpx;

				.icon {
					width: 38rpx;
					height: 25rpx;
					background-size: 100% 100%;
					background-repeat: no-repeat;
					margin-right: 25rpx;
				}
			}
		}
	}

	.navBar {
		position: fixed !important;
		top: 0;
		left: 0;
		// display: flex;
		// justify-content: center;
		width: 100%;
		height: 80rpx;
		background-color: transparent;
		z-index: 999;

		.contentWrap {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.leftContent {
				width: 100rpx;
				margin-left: 32rpx;
				height: 100%;
				display: flex;
				align-items: center;

				.backBtn {
					width: 67rpx;
					height: 48rpx;
				}

				.userBtn {
					width: 67rpx;
					height: 48rpx;
					marign-left: 32rpx;

					.login {
						#btnLogin {
							&.ios {
								font-weight: bold;
							}
							&::after {
								border: none;
							}
						}
					}
				}

				.headBtn {
					width: 64rpx;
					height: 64rpx;
					background-size: 100% 100%;
					background-repeat: no-repeat;
					border-radius: 50%;
				}

				.login {
					font-size: 30rpx;
					color: #000;
				}
			}

			.middleContent {
				font-size: 34rpx;
				&.ios {
					font-weight: bold;
				}
			}

			.wxBtn {
				width: 87rpx;
				height: 32rpx;
			}
		}
	}
	
	.cornerWrap, .cornerWrapXH {
		height: 16rpx;
		width: 24rpx;
		background-size: 100% 100%;
		background-repeat: no-repeat;
		position: absolute;
		top: 0;
		right: 138rpx;
	}
	
	.cornerWrapXH {
		width: 25rpx;
		height: 13rpx;
		left: 52rpx;
		right: auto;
	}
	
	.addGuidance, .addGuidanceXH {
		position: absolute;
		top: 15rpx;
		right: 45rpx;
		height: 121rpx;
		width: 320rpx;
		background-color: #5b5b5b;
		padding: 24rpx 0 24rpx 24rpx;
		box-sizing: border-box;
		color: #fff;
		display: flex;
		border-radius: 16rpx;
		// &::after {
		// 	content: '';
		// 	border: 24rpx solid transparent;
		// 	border-bottom-color: #5b5b5b;
		// 	position: absolute;
		// 	top: -23rpx;
		// 	right: 110rpx;
		// }
		.contentText {
			flex: 1;
			position: relative;
			.text_one {
				height: 40rpx;
				line-height: 40rpx;
				font-size: 28rpx;
				margin-bottom: 4rpx;
			}
			.text_two {
				height: 33rpx;
				line-height: 33rpx;
				font-size: 24rpx
			}
			.corner {
				position: absolute;
				top: -59rpx;
				right: 0;
				width: 24rpx;
				height: 24rpx;
				background-color: red;
				border: 24rpx solid transparent;
				border-bottom-color: #5b5b5b;
			}
		}
		.closeWrap {
			width: 64.5rpx;
			display: flex;
			justify-content: center;
			align-items: center;
			.close {
				width: 20rpx;
				height: 20rpx;
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
		}
	}
	
	.addGuidanceXH {
		top: 11rpx;
		background-color: rgba(50,50,50,0.96);
		width: 330rpx;
		
		.contentText {
			.text_one {
				height: 36rpx;
				line-height: 36rpx;
				font-size: 26rpx;
				margin-bottom: 4rpx;
			}
			.text_two {
				height: 36rpx;
				line-height: 36rpx;
				font-size: 26rpx;
			}
		}
	}
</style>
